<template>
    <div class="vnext">
        <el-form :inline="false" label-width="120px" class="text-left">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="DNS">
                        <el-input
                                v-model="sForm.address" v-setting>
                            <el-button slot="append" icon="el-icon-delete" @click="$emit('del-server', idx)"/>
                        </el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="DNS">
                        <el-input
                                v-model.number="sForm.port" v-setting>
                        </el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="domains">
                        <template v-slot:label>
                            <el-tooltip effect="light">
                                <div slot="content">
                                    <p>  </p>
                                    <ul>
                                        <li> "sina.com"  "sina.com""sina.com.cn" 
                                            "www.sina.com" "sina.cn"
                                        </li>
                                        <li> <code>"regexp:"</code> 
                                            "regexp:\\.goo.*\\.com$"  "www.google.com""fonts.googleapis.com"
                                            "google.com"
                                        </li>
                                        <li> <code>"domain:"</code> 
                                            "domain:v2ray.com"  "www.v2ray.com""v2ray.com" "xv2ray.com"
                                        </li>
                                        <li> <code>"full:"</code> 
                                            "full:v2ray.com"
                                             "v2ray.com"  "www.v2ray.com"
                                        </li>
                                        <li> <code>"geosite:"</code>  <code>geosite:google</code>
                                            
                                            <code>geosite:cn</code> <a target="_blank"
                                                                                 href="https://www.v2fly.org/config/routing.html#%E9%A2%84%E5%AE%9A%E4%B9%89%E5%9F%9F%E5%90%8D%E5%88%97%E8%A1%A8"></a>
                                        </li>
                                        <li> <code>"ext:file:tag"</code> <code>ext:</code>
                                            <a target="_blank" href="https://www.v2fly.org/config/env.html#"
                                               class=""></a> 
                                            <code>geosite.dat</code> 
                                        </li>
                                    </ul>
                                </div>
                                <label>domains:</label>
                            </el-tooltip>
                        </template>
                        <el-input type="textarea" rows="3" v-model="domainValue" placeholder="domains"
                                  v-setting></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="expectIPs:">
                        <template v-slot:label>
                            <el-tooltip effect="light">
                                <div slot="content">
                                    <p>V2Ray 4.22.0+ IP   </p>
                                    <p>V2Ray DNS  IP  expectIPs </p>
                                    <p> IP </p>
                                    <ul>
                                        <li>IP <code>"127.0.0.1"</code></li>
                                        <li><a href="https://en.wikipedia.org/wiki/Classless_Inter-Domain_Routing"
                                               target="_blank" rel="noopener noreferrer">CIDR
                                            <svg xmlns="http://www.w3.org/2000/svg" aria-hidden="true" x="0px" y="0px"
                                                 viewBox="0 0 100 100" width="15" height="15" class="icon outbound">
                                                <path fill="currentColor"
                                                      d="M18.8,85.1h56l0,0c2.2,0,4-1.8,4-4v-32h-8v28h-48v-48h28v-8h-32l0,0c-2.2,0-4,1.8-4,4v56C14.8,83.3,16.6,85.1,18.8,85.1z"></path>
                                                <polygon fill="currentColor"
                                                         points="45.7,48.7 51.3,54.3 77.2,28.5 77.2,37.2 85.2,37.2 85.2,14.9 62.8,14.9 62.8,22.9 71.5,22.9"></polygon>
                                            </svg>
                                        </a> <code>"10.0.0.0/8"</code>
                                        </li>
                                        <li>GeoIP <code>"geoip:cn"</code> <code>geoip:</code>
                                            <ul>
                                                <li><code>"geoip:private"</code>V2Ray 3.5+ <code>127.0.0.1</code>
                                                </li>
                                            </ul>
                                        </li>
                                        <li> IP <code>"ext:file:tag"</code> <code>ext:</code>
                                            <a href="/config/env.html#" class=""></a> 
                                            <code>geoip.dat</code> 
                                        </li>
                                    </ul>
                                </div>
                                <label>expectIPs:</label>
                            </el-tooltip>
                        </template>
                        <el-input type="textarea" rows="3" v-model="ipValue" placeholder="expectIPs" v-setting></el-input>
                    </el-form-item>
                </el-col>
            </el-row>



        </el-form>


    </div>
</template>

<script>


    export default {
        name: "DnsServerObject",
        model: {
            prop: 'setting',
            event: 'change'
        },
        methods: {

            getSettings() {
                if(this.sForm.port =="53" && this.sForm.domains.length==0 && this.sForm.expectIPs.length==0){
                    return "" + this.sForm.address;
                }
                return Object.assign({}, this.sForm);
            },
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            fillDefaultValue(setting) {
                if(typeof(setting)==="string"){
                    setting = {"address": setting};
                }

                Object.assign(this.sForm, setting);
                this.$nextTick().then(() => {
                    this.formChanged();
                })
            }
        },
        watch: {
            setting(val) {
                if (this.changedByForm) {
                    this.changedByForm = false;
                    return;
                }
                this.fillDefaultValue(val);
            }
        },

        created() {
            this.fillDefaultValue(this.setting);
        },
        mounted() {
            // $(this.$el).on("change", "input", ()=>{
            //     this.formChanged();
            // });
        },

        data() {
            return {
                changedByForm: false,
                sForm: {
                    "address": "",
                    "port": 53,
                    "domains": [
                    ],
                    "expectIPs": [
                    ]
                }
            }
        },
        props: {
            setting: {
                default() {
                    return "";
                }
            },
            idx: {
                type: Number,
            }
        },
        computed: {
            domainValue: {
                get() {
                    if (!this.sForm.domains) {
                        return "";
                    }
                    return this.sForm.domains.join("\n");
                },
                set(val) {
                    if(val==""){
                        this.sForm.domains = [];
                        return;
                    }
                    this.sForm.domains = val.split("\n");
                }
            },
            ipValue: {
                get() {
                    if (!this.sForm.expectIPs) {
                        return "";
                    }
                    return this.sForm.expectIPs.join("\n");
                },
                set(val) {
                    if(val==""){
                        this.sForm.expectIPs = [];
                        return;
                    }
                    this.sForm.expectIPs = val.split("\n");
                }
            },
        },
    }
</script>

<style scoped>
    .vnext {
        margin-bottom: 20px;
        border-bottom: 1px solid #8c939d;
    }
</style>
