<template>
    <setting-card title="mKCP" :enable-setting.sync="enableSetting" @update:enableSetting="enableSettingChanged">
        <el-form :inline="false" label-width="90px" class="text-left">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="mtu">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content">maximum transmission unit 576 - 1460  1350</div>
                                <label>mtu</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.mtu" v-setting/>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="uplinkCapacity" label-width="130px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content"> MB/s 5 Byte  bit 0</div>
                                <label>uplinkCapacity</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.uplinkCapacity" v-setting/>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="readBufferSize" label-width="120px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content"> MB 2</div>
                                <label>readBufferSize</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.readBufferSize" v-setting/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="tti">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content">transmission time intervalmsmKCP  10 - 100  50</div>
                                <label>tti</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.tti" v-setting/>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="downlinkCapacity" label-width="130px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content"> MB/s 20 Byte  bit 0</div>
                                <label>downlinkCapacity</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.downlinkCapacity" v-setting/>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="writeBufferSize" label-width="120px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content"> MB 2</div>
                                <label>writeBufferSize</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.writeBufferSize" v-setting/>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="seed">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content">v4.24.2+ AES-128-GCM "NewAEADAESGCMBasedOnSeed Used"</div>
                                <label>seed</label>
                            </el-tooltip>
                        </template>
                        <el-input v-model="sForm.seed" v-setting/>
                    </el-form-item>
                </el-col>

                <el-col :span="8">
                    <el-form-item label="" label-width="130px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content">
                                    <ul>
                                        <li><code>"none"</code></li>
                                        <li><code>"srtp"</code> SRTP  FaceTime</li>
                                        <li><code>"utp"</code> uTP  BT </li>
                                        <li><code>"wechat-video"</code></li>
                                        <li><code>"dtls"</code> DTLS 1.2 </li>
                                        <li><code>"wireguard"</code> WireGuard  WireGuard </li>
                                    </ul>
                                </div>
                                <label></label>
                            </el-tooltip>
                        </template>
                        <el-select v-model="sForm.header.type" v-setting>
                            <el-option value="none"/>
                            <el-option value="srtp"/>
                            <el-option value="utp"/>
                            <el-option value="wechat-video"/>
                            <el-option value="dtls"/>
                            <el-option value="wireguard"/>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="congestion" label-width="120px">
                        <template v-slot:label>
                            <el-tooltip placement="top" effect="light">
                                <div slot="content"> falseV2Ray </div>
                                <label>congestion</label>
                            </el-tooltip>
                        </template>
                        <el-switch v-model="sForm.congestion" v-setting/>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>

        <p>mKCP  UDP  TCP mKCP mKCP  TCP </p>

    </setting-card>
</template>

<script>
    export default {
        name: "DomainSocketObject",
        model: {
            prop: 'setting',
            event: 'change'
        },
        data() {
            return {
                enableSetting: false,
                changedByForm: false,
                sForm: {
                    "mtu": 1350,
                    "tti": 20,
                    "uplinkCapacity": 5,
                    "downlinkCapacity": 20,
                    "congestion": false,
                    "readBufferSize": 1,
                    "writeBufferSize": 1,
                    "header": {
                        "type": "none"
                    },
                    "seed": ""
                }

            }
        },
        watch: {
            setting: {
                handler(val) {
                    if (this.changedByForm) {
                        this.changedByForm = false;
                        return;
                    }
                    this.fillDefaultValue(val);
                },
                deep: false
            }
        },
        created() {
            let setting = this.setting || {};
            this.fillDefaultValue(setting);

        },
        mounted() {
        },
        methods: {
            fillDefaultValue(setting) {
                setting = this.setting || {};
                if (this._.isEmpty(setting)) {
                    this.enableSetting = false;
                } else {
                    this.enableSetting = true;
                }
                Object.assign(this.sForm, setting);

                this.formChanged();
            },

            getSettings() {
                if (!this.enableSetting) {
                    return null;
                }
                let setting = Object.assign({}, this.sForm);
                return setting;
            },
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            enableSettingChanged() {
                this.$nextTick(() => {
                    this.formChanged();
                });

            },
        },
        props: {
            setting: {
                type: Object,
            }
        }
    }
</script>

<style scoped>

</style>
