<template>
    <setting-card title="" :show-enable="false">
        <el-form ref="form" label-width="80px">
            <el-form-item label="">
                <template v-slot:label>
                    <el-tooltip>
                        <div slot="content">"/tmp/v2ray/_access.log"Linux"C:\\Temp\\v2ray\\_access.log"Windows
                            <br/> stdoutV2Ray 4.20 none access log</div>
                        <label></label>
                    </el-tooltip>
                </template>
                <el-input v-model="sForm.access" v-setting></el-input>
            </el-form-item>
            <el-form-item label="">
                <template v-slot:label>
                    <el-tooltip >
                        <div slot="content" >"/tmp/v2ray/_error.log"Linux"C:\\Temp\\v2ray\\_error.log"Windows
                            <br/> stdoutV2Ray 4.20 none error logloglevel: "none"</div>
                        <label></label>
                    </el-tooltip>
                </template>
                <el-input v-model="sForm.error" v-setting></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="sForm.loglevel" placeholder="" v-setting>
                    <el-option v-for="level of levels" :label="level" :value="level" :key="level"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
    </setting-card>

</template>

<script>
    import SettingCard from "@/components/SettingCard";
    import * as G from '@/consts'

    export default {
        name: "LogSetting",
        components: {SettingCard},
        model:{
            prop:"log",
            event:"change"
        },
        data() {
            return {
                changedByForm: false,
                levels: G.LOG_LEVELS,
                sForm: {
                    "access": "",
                    "error": "",
                    "loglevel": "warning"
                }
            }
        },
        created() {
            const settings = this.log || {};
            Object.assign(this.sForm, settings);
        },
        mounted() {

        },
        methods: {
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            getSettings() {
                return Object.assign({},this.sForm);
            },
        },
        watch: {
            log: {
                handler: function (val) {
                    if(this.changedByForm){
                        this.changedByForm = false;
                        return;
                    }
                    Object.assign(this.sForm, val);
                },
                deep: false
            }
        },
        props: {
            log: {
                type: Object,
            }
        }
    }
</script>

<style scoped>
    .el-select {
        width: 100%;
    }
</style>
