<template>
    <div>
        <el-form :inline="false" label-width="110px" class="text-left">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="network">
                        <el-select v-model="sForm.network" v-setting>
                            <el-option value="tcp"/>
                            <el-option value="udp"/>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="address">
                        <el-input v-model="sForm.address" v-setting/>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="port">
                        <el-input v-model.number="sForm.port" v-setting/>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <p>DNS  DNS  DNS  UDP  TCP </p>
        <p> DNS  IP  A  AAAA <a href="https://www.v2fly.org/config/dns.html" class="">DNS </a></p>
        <p>DNS  V2Ray 4.15 </p>
    </div>
</template>

<script>
    export default {
        name: "OutboundDNS",
        model: {
            prop: 'setting',
            event: 'change'
        },
        methods: {

            getSettings() {
                return Object.assign({}, this.sForm);
            },
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            fillDefaultValue(setting) {
                setting = this.setting || {};
                setting = this._.pick(setting, ["network", "address", "port"]);
                this.sForm = this._.defaults(setting, this.sForm);
                this.$nextTick().then(()=>{
                    this.formChanged();
                });
            }
        },
        watch: {
            setting(val) {
                if (this.changedByForm) {
                    this.changedByForm = false;
                    return;
                }
                this.fillDefaultValue(val);
            }
        },

        created() {
            this.fillDefaultValue(this.setting);
        },
        mounted() {
            // $(this.$el).on("change", "input", ()=>{
            //     this.formChanged();
            // });
        },

        data() {
            return {
                changedByForm: false,
                sForm: {
                    "network": "tcp",
                    "address": "8.8.8.8",
                    "port": 53
                }
            }
        },
        props: {
            setting: {
                type: Object
            }
        },
        computed: {},
    }
</script>

