import axios from 'axios';

function getBaseURL() {
    let baseURL = localStorage.getItem("baseURL") || "http://localhost:8035/v2ray";
    return baseURL;
}

let apiAuth = localStorage.getItem("api_auth");
if(apiAuth) {
    try{
        axios.defaults.auth = JSON.parse(apiAuth);
    }catch (e) {
        console.log("apiAuth", e.toString());
    }
}
axios.defaults.baseURL = getBaseURL();
// axios.defaults.auth  = {
//     username: 'admin',
//     password: 'admin'
// };
// 
axios.interceptors.response.use(function (response) {
    // 
    return response;
}, function (error) {
    if(error.response && error.response.status=="401"){
        ajax.openLoginForm();
    }
    return Promise.reject(error);
});

class Ajax {
    constructor(){

    }
    async post(url, data, options = {}) {
        try {
            return await axios.post(url, data, options);
        } catch (e) {
            return {
                status: 500,
                statusText: e.toString(),
            }
        }

    }
    async get(url, options) {
        try {
            return await axios.get(url, options);
        } catch (e) {
            return {
                status: 500,
                statusText: e.toString(),
            }
        }
    }
    setBaseURL(baseURL) {
        axios.defaults.baseURL = baseURL;
        localStorage.setItem("baseURL", baseURL);
    }
    getBaseURL(){
        return getBaseURL();
    }
    openLoginForm() {
    }
    setAuth(userName, password) {
        if(userName){
            axios.defaults.auth = {
                username: userName,
                password: password
            }
            localStorage.setItem("api_auth", JSON.stringify(axios.defaults.auth));
        }else{
            delete axios.defaults.auth;
            localStorage.removeItem("api_auth");
        }

    }
}
const ajax = ((function(){
    let ajax= null;
    return function(){
        if(ajax==null){
            ajax = new Ajax();
        }
        return ajax;
    }
})()());

export default ajax;
