<template>
    <setting-card title="QUIC" :enable-setting.sync="enableSetting" @update:enableSetting="enableSettingChanged">
        <el-form :inline="true" label-width="90px" class="text-left">
            <el-form-item label="security">
                <template v-slot:label>
                    <el-tooltip placement="top" effect="light">
                        <div slot="content"> QUIC </div>
                        <label>security</label>
                    </el-tooltip>
                </template>
                <el-select v-model="sForm.security" v-setting>
                    <el-option value="none"/>
                    <el-option value="aes-128-gcm"/>
                    <el-option value="chacha20-poly1305"/>
                </el-select>
            </el-form-item>
            <el-form-item label="key">
                <template v-slot:label>
                    <el-tooltip placement="top" effect="light">
                        <div slot="content"> security  "none" </div>
                        <label>key</label>
                    </el-tooltip>
                </template>
                <el-input v-model="sForm.key" v-setting/>
            </el-form-item>
            <el-form-item label="">
                <template v-slot:label>
                    <el-tooltip placement="top" effect="light">
                        <div slot="content">
                            <ul>
                                <li><code>"none"</code></li>
                                <li><code>"srtp"</code> SRTP  FaceTime</li>
                                <li><code>"utp"</code> uTP  BT </li>
                                <li><code>"wechat-video"</code></li>
                                <li><code>"dtls"</code> DTLS 1.2 </li>
                                <li><code>"wireguard"</code> WireGuard  WireGuard </li>
                            </ul>
                        </div>
                        <label></label>
                    </el-tooltip>
                </template>
                <el-select v-model="sForm.header.type" v-setting>
                    <el-option value="none"/>
                    <el-option value="srtp"/>
                    <el-option value="utp"/>
                    <el-option value="wechat-video"/>
                    <el-option value="dtls"/>
                    <el-option value="wireguard"/>
                </el-select>
            </el-form-item>
        </el-form>
        <p>QUIC  Quick UDP Internet Connection Google  UDP :</p>
        <ol>
            <li>1-RTT  0-RTT</li>
            <li> TCP </li>
            <li> Wifi  4G </li>
        </ol>
        <p>QUIC  IETF </p>
    </setting-card>
</template>

<script>
    export default {
        name: "DomainSocketObject",
        model: {
            prop: 'setting',
            event: 'change'
        },
        data() {
            return {
                enableSetting: false,
                changedByForm: false,
                sForm: {
                    "security": "none",
                    "key": "",
                    "header": {
                        "type": "none"
                    }
                }

            }
        },
        watch: {
            setting: {
                handler(val) {
                    if (this.changedByForm) {
                        this.changedByForm = false;
                        return;
                    }
                    this.fillDefaultValue(val);
                },
                deep: false
            }
        },
        created() {
            let setting = this.setting || {};
            this.fillDefaultValue(setting);

        },
        mounted() {
        },
        methods: {
            fillDefaultValue(setting) {
                setting = this.setting || {};
                if (this._.isEmpty(setting)) {
                    this.enableSetting = false;
                } else {
                    this.enableSetting = true;
                }
                Object.assign(this.sForm, setting);

                this.formChanged();
            },

            getSettings() {
                if (!this.enableSetting) {
                    return null;
                }
                let setting = Object.assign({}, this.sForm);
                return setting;
            },
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            enableSettingChanged() {
                this.$nextTick(() => {
                    this.formChanged();
                });

            },
        },
        props: {
            setting: {
                type: Object,
            }
        }
    }
</script>

<style scoped>

</style>
