<template>
    <setting-card title="Domain Socket" :enable-setting.sync="enableSetting" @update:enableSetting="enableSettingChanged">
        <el-form :inline="true" label-width="90px" class="text-left">
            <el-form-item label="path">
                <template v-slot:label>
                    <el-tooltip placement="top" effect="light">
                        <div slot="content"> V2Ray </div>
                        <label>path</label>
                    </el-tooltip>
                </template>
                <el-input v-model="sForm.path" v-setting/>
            </el-form-item>
            <el-form-item label="abstract" >
                <template v-slot:label>
                    <el-tooltip placement="top" effect="light">
                        <div slot="content"> abstract domain socket false</div>
                        <label>abstract</label>
                    </el-tooltip>
                </template>
                <el-switch v-model="sForm.abstract" v-setting/>
            </el-form-item>
        </el-form>
        <p>Domain Socket  Unix domain socket local loopbackDomain socket </p>
        <p> Unix domain socket  macOS  Linux Windows </p>
        <p> domain socket  IP  domain socket </p>
    </setting-card>
</template>

<script>
    export default {
        name: "DomainSocketObject",
        model: {
            prop: 'setting',
            event: 'change'
        },
        data() {
            return {
                enableSetting: false,
                changedByForm: false,
                sForm: {
                    "abstract": false,
                    "path": "/path/to/ds/file",
                }

            }
        },
        watch: {
            setting: {
                handler(val) {
                    if (this.changedByForm) {
                        this.changedByForm = false;
                        return;
                    }
                    this.fillDefaultValue(val);
                },
                deep: false
            }
        },
        created() {
            let setting = this.setting || {};
            this.fillDefaultValue(setting);

        },
        mounted() {
        },
        methods: {
            fillDefaultValue(setting) {
                setting = this.setting || {};
                if (this._.isEmpty(setting)) {
                    this.enableSetting = false;
                } else {
                    this.enableSetting = true;
                }
                Object.assign(this.sForm, setting);

                this.formChanged();
            },

            getSettings() {
                if (!this.enableSetting) {
                   return null;
                }
                let setting = Object.assign({}, this.sForm);
                return setting;
            },
            formChanged() {
                this.changedByForm = true;
                this.$emit("change", this.getSettings());
            },
            enableSettingChanged() {
                this.$nextTick(() => {
                    this.formChanged();
                });

            },
        },
        props: {
            setting: {
                type: Object,
            }
        }
    }
</script>

